<template>
  <div class="research-view">
    <StatusBar :farm="farm"></StatusBar>

    <ResearchCard v-if="lab.researchById(selectedResearch) !== undefined"
                  :research="lab.researchById(selectedResearch)" :farm="farm" />
    <div v-else class="no-selected">
      <h1>Select a research</h1>
    </div>
    <div class="thumb-lists">
      <div class="farm-window thumb-list-border">
        <h2>Available</h2>
        <div class="thumb-list">
          <template v-for="research in lab.available">
            <img class="research-thumb"
                 :src="research.image"
                 @click="this.selectResearch(research)"
                 :class="{ 'selected-thumb': this.selectedResearch === research.id }"
                 :title="research.name"
                 ondragstart="return false"
            />
          </template>
        </div>
      </div>
      <div class="farm-window thumb-list-border">
        <h2>Completed</h2>
        <div class="thumb-list">
          <template v-for="research in lab.completed">
            <img class="research-thumb"
                 :src="research.image"
                 @click="this.selectResearch(research);"
                 :class="{ 'selected-thumb': this.selectedResearch === research.id }"
                 :title="research.name"
                 ondragstart="return false"
            />
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Farm from '@/Farm'
import EmployeeCard from '@/components/EmployeeCard.vue'
import ValueMeter from '@/components/ValueMeter.vue'
import Laboratory from '@/Laboratory'
import ResearchCard from '@/components/ResearchCard.vue'
import Research from '@/Research'
import FarmStatusCard from '@/components/FarmStatusCard.vue'
import TextButton from '@/components/TextButton.vue'
import util from '@/util'
import StatusBar from '@/components/StatusBar.vue'

@Options({
  components: {
    ResearchCard, ValueMeter, FarmStatusCard, TextButton, StatusBar
  },
  props: {
    farm: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    selectResearch(research: Research) {
      console.log('Selecting', research)
      this.selectedResearch = research.id
      //this.farm.research(this.selectedResearch)
    },
    util() {
      return util
    }
  },
  computed: {
  },
  async created() {
    this.lab = this.farm.state.lab
    this.selectedResearch = ''
  },
  activated() {
    console.log('Activated research tab')
    this.$forceUpdate()
  }
})
export default class ResearchView extends Vue {
  farm!: Farm
  lab = new Laboratory()
  selectedResearch: string = ''
}
</script>

