<template>
  <template v-if="isLoaded">
    <div class="menu-view">
      <div class="farm-window title-window">
        <div class="stat-row" style="grid-template-columns: 1fr;">
          <h1 style="text-align: center;">Milkfarm</h1>
        </div>
        <div>
          <p>Welcome!</p>
          <p>This is your very own dairy farm. Take a moment, enjoy the peace and quiet of the fields and forests.
          There couldn't be a calmer and more relaxing place.</p>
          <p>Now, go out there and bring some noise! You'll need to hire staff and research technology if you ever want to produce some milk!</p>
        </div>
        <div class="stat-row blushing-defeat-button-row">
          <TextButton class="farm-button" @click="window().location.href = 'https://www.blushingdefeat.com/play/'">
            <h3>Exit to Blushing Defeat</h3>
          </TextButton>
        </div>
      </div>
      <div class="farm-window saves-window">
        <div class="stat-row">
          <h3 style="text-align: left;">Save Menu</h3>
          <p style="text-align: right;">Version {{ farm.state.gameVersion }}</p>
        </div>
        <div class="stat-row">
          <input v-model="farm.state.saveName">
          <TextButton class="farm-button" @click="writeSave(farm.state.saveName)"><h3>Save</h3></TextButton>
        </div>

        <div class="farm-window saves-list">
          <div class="flex-wrapper">
            <div class="save-entry new-game-save">
              <TextButton @click="this.farm.createNewSave(); $forceUpdate()"
                          class="load-button farm-button new-game-button">
                <h3>New Game</h3>
              </TextButton>
            </div>

            <div v-for="(save, saveName) in readSaves()" :key="saveName"
                 class="save-entry">
              <TextButton @click="loadSave(saveName); $forceUpdate()"
                          class="load-button farm-button">
                <div class="save-info">
                  <h3>{{ saveName }}</h3>
                  <p><i>{{util().currentDate(save.time)}}</i></p>
                </div>
              </TextButton>
              <div class="save-info">
                <p>Version {{ save.gameVersion }}</p>
                <p>{{ date(save.saveDate) }}</p>
              </div>
              <TextButton @click="deleteSave(saveName); $forceUpdate()"
                          class="farm-button delete-save-button">
                <h3>X</h3>
              </TextButton>
            </div>
          </div>
        </div>
        <!--
        <div class="stat-row clear-all-button-row">
          <TextButton class="farm-button" @click="farm.clearSaves(); $forceUpdate()"><h3>Clear All</h3></TextButton>
        </div>-->
      </div>
    </div>
  </template>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Farm from '@/Farm'
import EmployeeCard from '@/components/EmployeeCard.vue'
import ValueMeter from '@/components/ValueMeter.vue'
import util from '../util'
import FarmStatusCard from '@/components/FarmStatusCard.vue'
import StatusBar from '@/components/StatusBar.vue'
import TextButton from '@/components/TextButton.vue'

@Options({
  components: {
    EmployeeCard, ValueMeter, FarmStatusCard, StatusBar, TextButton
  },
  props: {
    farm: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    util() {
      return util
    },
    readSaves() {
      let saves = this.farm.readSavesFromLocalStorage()
      delete saves['New Game']
      return saves
    },
    loadSave(saveName: string) {
      this.farm.loadFromLocalStorage(saveName)
    },
    writeSave(saveName: string) {
      if (saveName === 'New Game') return
      this.farm.save(this.farm.state.saveName)
      this.forceUpdate()
    },
    deleteSave(saveName: string) {
      this.farm.deleteSave(saveName)
    },
    forceUpdate() {
      this.$forceUpdate()
    },
    date(unixTime: number) {
      let date = new Date(unixTime)
      return date.toLocaleString('sv-SE', { hour12: false, year: 'numeric', month: 'numeric', day: 'numeric', hour: '2-digit', minute: '2-digit' })
    },
    window() {
      return window
    }
  },
  async created() {
    //let farm = new Farm()
    //await farm.load()
    //this.farm = farm
    this.isLoaded = true
  }
})
export default class Menu extends Vue {
  isLoaded = false
  farm!: Farm
}
</script>

